<template>
<div class='career'>
	<section class='section_ca'>
    	<div class="left">
        	<div class="inner"><h3>天道酬勤</h3><p>知行合一</p></div>
        </div>
        <div class="right">
        	<div class='inner'><h3>我是王昢</h3><p>来自交大</p></div>
        </div>
    </section>
      <div id="categories-container">
    	<h3 class="section-title">I want to work with<br />
        	<span>the world's best</span>
        </h3>
    </div>

    <div class="type3">
				<div class="module">
                    <div class="image">
                        <img src="../assets/img/bg_experienced.jpg" />
                    </div>
                    <div class="content_ca">
                        <div class="title_ca">
                            <h3>Experienced</h3>
                        </div>
                        <p>我去!快要失业怎么办怎么办？当下流的泪都是选专业的时候脑子进的水，如今这样尴尬困窘的局面也只能怪自己</p>
                        </a><a href="javascript:void(0);" class="read-more">Read more</a>
                    </div>
                </div>

            <div class="module">
            	<div class="image">
            		<img src="../assets/img/bg_early_careers.jpg" />
                </div>
                <div class="content_ca">
                    <div class="title_ca">
                        <h3>Early Careers</h3>
                    </div>
                    <p>如果想入互联网的坑，对于非科班来说实在是很不友好啊，所谓名校的皮很多时候可能更是负累，倒还不如直接放养拉倒，不过话说回来，好的985本来就是让你科研的</p>
                        </a><a href="javascript:void(0);" class="read-more">Read more</a>
                </div>
            </div>

            <div class="module">
            	<div class="image">
            		<img src="../assets/img/bg_students.jpg" />
                </div>
                <div class="content_ca">
                    <div class="title_ca">
                         <h3>Students</h3>
                    </div>
                    <p>如果不想挤千军万马的那个独木桥，直接选择对口专业就业那简直就是直接飞升成仙，了却一切烦恼，这年头为了钱，大家也是没有办法啊</p>
                    <a href="javascript:void(0);" class="read-more">Read more</a>
                </div>
            </div>

    </div>
</div>
</template>

<script>
	export default{
		mounted(){
			let left=document.getElementsByClassName('left')[0]
			let right=document.getElementsByClassName('right')[0]

			left.onmouseenter=function(){
				toggle_class(left,'h_left')
				toggle_class(right,'nh_right')
			}
			left.onmouseleave=function(){
				toggle_class(left,'h_left')
				toggle_class(right,'nh_right')
			}

			right.onmouseenter=function(){
				toggle_class(left,'nh_left')
				toggle_class(right,'h_right')
			}
			right.onmouseleave=function(){
				toggle_class(left,'nh_left')
				toggle_class(right,'h_right')
			}

			function toggle_class(object,class_name) {
				let current_class=object.getAttribute('class')
				if (current_class.indexOf(class_name)!=-1){
					object.setAttribute('class',current_class.replace(class_name,''))
				}
				else {
					if(current_class[current_class.length-1]==' '){
						object.setAttribute('class',current_class+class_name)
					}
					else
						object.setAttribute('class',current_class+" "+class_name)
				}
			}
		}
	}
</script>

<style>
.section-title {
	padding:50px 0;
	font-family:"Gill Sans Alt One WGL W01 Lt",Calibri,sans-serif;
	max-width:600px;
	margin-left:auto;
	margin-right:auto;
	font-size:48px;
	font-weight:400;
	line-height:100%;
	text-align:center;
}

#categories-container {
	height:100px;
	//width:1313px;
	width:100%;

	position:relative;
	top:620px;
	z-index:-1;
}

.type3 {
	padding:0 20px;
	position:absolute;
	top:850px;
	z-index:-3;
	left:70px;
}

div .image:hover img {
	transform:scale(1.1);
	-webkit-transform:scale(1.1);
}

.image img {
	-webkit-transition:all 1s ease;
	-moz-transition:all 1s ease;
	-ms-transition:all 1s ease;
	-o-transition:all 1s ease;
	transition:all 1s ease;
}

div .module{
	position:relative;
	//height:650px!important;
	height:40%;
	//width:387px;
	width:30%;
	float:left;
}

.title_ca {
	color:#348faa;
}

.title_ca h3 {
	margin-bottom:20px;
	font-size:28px;
	font-family:"Gill Sans Alt One WGL W01 Rg",Calibri,sans-serif;
	font-weight:400;
}

.read-more {
	position:relative;
	color:#11809F;
}

.read-more:hover {
	color:#D77B00;
}


div .image {
	position:relative;
	//height:350px!important;
	height:50%;
	//width:387px;
	width:100%;
	border:0;
	overflow:hidden;
	max-width:100%;
	height:auto;
	float:left;
}

div .image img{
	//height:350px!important;
	//width:387px;
	height:50%;
	//width:387px;
	width:100%;
}
a:link {
 text-decoration: none;
}

.content_ca{
	background-color:#e6eaeb;
	padding:30px;
	height:580px;

}

.content_ca p{
	color:#969696;
	font:'腾祥小新体繁';
}
/***********************************************************/
.career {
	position:absolute;
	width:100%;
	height:100%;
}
.left{
	background-image: url(../assets/img/enter_culture.jpg);
	background-position-x: left;
    background-position-y: 0px;
	background-size:cover;
	height:100%;
	width:50%;
	float:left;
	overflow:hidden;
	position:absolute;
	left:0;
	transition:width 1.5s ease;
	display:block;

}
.inner{
	width:100%;
	height:100%;
	position:absolute;
	top:35%;
	text-align:center;
	font: 45px '汉仪全唐诗繁';
	//font-size:45px;
	color:white;
}
.right{
	height:100%;
	background-image: url(../assets/img/enter_impact.jpg);
	background-position-x: right;
    background-position-y: 0px;
	background-size:cover;
	height:100%;
	width:50%;
	float:right;
	overflow:hidden;
	position:absolute;
	right:0;
	display:block;
	transition:width 1.5s ease;
}

.h_left,.h_right {
	width:67%;
}
.nh_left,.nh_right {
	width:34%;
}

.section_ca {
	width:100%;
	height:580px;
	//height:70%;
	position:absolute;
}
</style>



