<template>
  <div class="bounce">
  <div class='my_ball'>
  </div>
  </div>
</template>
<script>
  export default {
    mounted(){
      let new_x=0
      let new_y=0
      let ori_x=0
      let ori_y=0
      let v_x=0
      let v_y=0
      let o_x=0
      let o_y=0
      let pos_x=0
      let pos_y=0
      let ball=document.getElementsByClassName('my_ball')[0]
      console.log(ball)
      ball.onmousedown=function(e){
        let event=e||window.event
        let self=this
        let optim_x=event.clientX-this.offsetLeft
        let optim_y=event.clientY-this.offsetTop
        document.onmousemove=function(e){
          let event=e||window.event
          new_x=event.clientX-optim_x
          new_y=event.clientY-optim_y
          //console.log(new_x)
          ball.style.left=new_x+'px'
          ball.style.top=new_y+'px'
          v_x=new_x-o_x
          v_y=new_y-o_y
          o_x=new_x
          o_y=new_y
        }
      }
      ball.onmouseup=function(e){
        document.onmousemove=null
        startmove(e)
      }
      function startmove(e) {
        let gravity=2.5;
        let new_px=new_x
        let new_py=new_y
        let move
        move=setInterval(function run(){
          v_y+=gravity
          pos_x = ball.offsetLeft + v_x;
          pos_y = ball.offsetTop + v_y;
          if (pos_x<0||pos_x>window.innerWidth-ball.offsetHeight){
            v_x*=-1
            v_x*=0.9
            if (pos_x<0){
              pos_x=0
            }
            else{
              pos_x=window.innerWidth-ball.offsetHeight
            }
          }
          if (pos_y<0){
            pos_y=0
            v_y*=-1
            v_y*=0.9
          }

          if (Math.abs(v_x)<1){
            v_x=0
          }
          if(Math.abs(v_y)<1){
            v_y=0
          }
          if (pos_y>=window.innerHeight - ball.offsetHeight){
            v_y*=-1
            v_y*=0.9
            v_x*=0.9
            pos_y=window.innerHeight - ball.offsetHeight
            if (Math.abs(v_x)<1&&Math.abs(v_y)<1){
              clearInterval(move)
            }
          }
          ball.style.left=pos_x+'px'
          ball.style.top=pos_y+'px'

        },10)
      }
    }
  }

</script>
<style>
  .bounce {
    position: absolute;
    width: 100%;
    height:100%;
  }
  .my_ball {
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: 200px;
    background: linear-gradient(to top left,indianred,green,#CEE100,green,gold);
    border-radius: 50%;
  }
</style>



